import {Divider} from "antd";
import React from "react";
import Spinner from "../../components/Spinner/Spinner";
import {useGalleries} from "../../contentful/useGalleries";
import "./Gallery.scss";
import {GalleryItem} from "./GalleryItem";
import {Helmet} from "react-helmet-async";
import {
  FacebookOutlined,
  InstagramOutlined,
  YoutubeOutlined,
} from "@ant-design/icons";

const Gallery: React.FC = () => {
  const galleriesData = useGalleries();

  if (galleriesData.loading) {
    return <Spinner />;
  }

  return (
    <div className={"gallery"}>
      <Helmet>
        <title>Gallery - Kerala Samajam Hamburg</title>
      </Helmet>
      <Divider>
        <h2>Gallery</h2>
      </Divider>
      <div className={"links"}>
        <div>
          <InstagramOutlined />
          <a
            href="https://www.instagram.com/ksh_hamburg/"
            target="_blank"
            rel="noopener noreferrer"
          >
            Instagram - KSH
          </a>
        </div>
        <div>
          <FacebookOutlined />{" "}
          <a
            href="https://www.facebook.com/KeralaSamajamHamburg/"
            target="_blank"
            rel="noopener noreferrer"
          >
            Facebook - KSH
          </a>
        </div>
        <div>
          <YoutubeOutlined />{" "}
          <a
            href="https://www.youtube.com/channel/UCfO4c84YkrDPppGktKq-WRA/videos"
            target="_blank"
            rel="noopener noreferrer"
          >
            YouTube - KSH
          </a>
        </div>
      </div>

      {galleriesData.galleries.map((gallery, index) => {
        return <GalleryItem gallery={gallery} key={index} />;
      })}
    </div>
  );
};
export default Gallery;
